
.about__container{
    grid-template-columns: repeat(2,1fr);
    column-gap: 4rem;
    align-items: center;
}

.about__img{
    width: 350px;
    border-radius: 1.5rem;
    justify-self: center;
}

.about__title{
    font-size: var(--small-font-size);
    font-weight: var(--font-medium);
    
}

.about__subtitle{
    font-size: var(--tiny-font-size);
    
}
.about__description{
    align-items: center;
    padding: 0 4rem 0 0;
    margin-bottom:var(--mb-2-5);
}


@media screen and (max-width: 992px) {
  .about__container{
    grid-template-columns: 1fr;
    column-gap: 2.5rem;
  }
  .about__img{
    width: 220px;
  }
  .about__data{
    text-align: center;
  }
  .about__description{
    padding: 0 5rem;
    margin-bottom: 2rem;
  }
}
 
  
  
